var _this;
import wInput from '../../components/watch-login/watch-input.vue'; //input
import wButton from '../../components/watch-login/watch-button.vue'; //button

export default {
	data() {
		return {
			phoneData: '', // 用户/电话
			passData: '', //密码
			verCode: '', //验证码
			showAgree: false, //协议是否选择
			isRotate: false //是否加载旋转
		};
	},
	components: {
		wInput,
		wButton
	},
	mounted() {
		_this = this;
	},
	methods: {
		isShowAgree() {
			//是否选择协议
			_this.showAgree = !_this.showAgree;
		},
		getVerCode() {
			//获取验证码
			if (_this.phoneData.length != 11) {
				this.$refs.uToast.show({
					title: '手机号不正确',
					type: 'default',
					position: 'bottom'
				});
				return false;
			}
			console.log('获取验证码');
			// this.$refs.runCode.$emit('runCode'); //触发倒计时（一般用于请求成功验证码后调用）
			// uni.showToast({
			// 	icon: 'none',
			// 	position: 'bottom',
			// 	title: '模拟倒计时触发'
			// });

			// setTimeout(function() {
			// 	_this.$refs.runCode.$emit('runCode', 0); //假装模拟下需要 终止倒计时
			// 	uni.showToast({
			// 		icon: 'none',
			// 		position: 'bottom',
			// 		title: '模拟倒计时终止'
			// 	});
			// }, 3000);
		},
		startReg() {
			//注册
			if (this.isRotate) {
				//判断是否加载中，避免重复点击请求
				return false;
			}
			if (this.showAgree == false) {
				this.$refs.uToast.show({
					title: '请先同意《协议》',
					type: 'default',
					position: 'bottom'
				});
				return false;
			}
			if (this.phoneData.length != 11) {
				this.$refs.uToast.show({
					title: '手机号不正确',
					type: 'default',
					position: 'bottom'
				});
				return false;
			}
			if (this.passData.length < 6 || this.passData.length > 20) {
				this.$refs.uToast.show({
					title: '请输入6位到20位的密码',
					type: 'default',
					position: 'bottom'
				});
				return false;
			}
			if (this.verCode.length != 4) {
				this.$refs.uToast.show({
					title: '验证码不正确',
					type: 'default',
					position: 'bottom'
				});
				return false;
			}
			console.log('注册成功');
			_this.isRotate = true;
			setTimeout(function() {
				_this.isRotate = false;
			}, 3000);
		}
	}
};
